@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+TC&display=swap');
body {	
	width: 100vw;
	height: 100vh;

	/*background-image: url(https://t4.ftcdn.net/jpg/01/25/16/91/360_F_125169154_fcnAEc5TPRg19XcZYgKz20eEYeKi6qwc.jpg);*/	
	background-image: url("../imgs/bg.png");
	
	overflow: hidden;
}

@media (orientation: landscape) {
	body {
		background-size: cover;
		background-repeat: no-repeat;
		position: relative;		
		overflow: scroll;				
		height: max-content;
		min-height: 250vh;
	}
	.main_section{
		display: flex;			
	}
  }
  
  @media (orientation: portrait) {
	body {
		background-size: contain;
		background-repeat: round;
		position: fixed;
	}
  }


.main_section{
	/*background-color: aqua;*/
	width: 100%;
	height: 100%;
	z-index: 1;

	display:  grid;
	grid-template-rows: 200px auto 200px;  /* main content, btns , footer */
	position: absolute;
	overflow-y: auto;
}
.main_overlay-container{
	/*
	position:  absolute;
	left: 50%;
	bottom: 5%;
	transform: translate(-50%, -5%);
	background-color: aquamarine;
	max-height: 40%;
	*/
	width: 95%;
	flex-grow: 1;
	flex-direction: column;
	justify-content: center;

	justify-content: center;
    align-items: center;
    display: flex;
}
.main_center_layout{
	position: static;
    overflow-y: scroll;

	flex-grow: 1;
	flex-direction: column;
	justify-content: space-evenly;
	align-items: center;	
	
	height: 100%;
	width: 100%;
	
	/*
	display: flex;
	position: absolute;
	top: 5%;
	left: 50%;
	transform: translate(-50%, -5%);
	background-color: brown;
	*/
}
.main_footer{
	background-color: aqua;
	z-index: -1;
}
.main_center_layout * {
	margin: 1%;
}

/* STAMP IMAGE */
.item-thumb{	
	height: 150px;	
	text-align: center;
}
.item{
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}
/* Main stamps container */
.main_item-container{

	justify-content: center;
	align-items: center;
	display: flex;
    flex-direction: row;
    flex-wrap: wrap;	
	
	/*width: 95%;*/
	min-width: 80%;
	height: auto;
	min-height: 45vh;
	/*
	max-height: 70%;
	background-color: rgba(215, 215, 215, 0.696);
	overflow-y: scroll;
	*/
	border-radius: 15px;	
	padding:  15px;

}

/* 下排操作按鈕 */
.main_footer_panle{
	width: 100%;
	height: auto;
	max-height: 200px;

	display: flex;
	flex-direction: column;
	
	justify-content: center;
	align-items: center;
}

.main_opt_btn{
	/*https://codepen.io/vinsalmont/pen/QGwGye?editors=1100*/
	max-width: 200px;
	margin: 10px;

	cursor: pointer;
	outline: none;
	  &.outline {
		  position: relative;
		  z-index: 3;
		  background: transparent;
		  color: #1172c4;
		  font-size: 14px;
		  border-color: #1172c4;
		  border-style: solid;
		  border-width: 2px;
		  border-radius: 22px;
		  padding: 10px 40px;
		  text-transform: uppercase;
		  transition: all 0.2s linear;
		  a {
			  text-decoration: none;
		  }
	  }
	  &.outline:hover {
		  color: white;
		  background: #1172c4;
		  border-color: white;
		  transition: all 0.2s linear;
	  }
	  &.outline:active {
		  border-radius: 22px;
	  }
	  &.white-blue {
        font-weight: 700;
        color: #00aeef;
        border-color: white;
        background: white;
    }
    &.white-blue:hover {
        color: white;
        background: #00aeef;
        border-color: white;
    }
    &.white-green {
        font-weight: 700;
        color: #7dc21e;
        border-color: white;
        background: white;
    }
    &.white-green:hover {
        color: white;
        background: #7dc21e;
        border-color: white;
    }
   &.green-white {
        font-weight: 700;
        color: #7dc21e;
        border-color: #7dc21e;
        background: transparent;
    }
    &.green-white:hover {
        color: white;
        background: #7dc21e;
        border-color: #7dc21e;
    }
    &.purple-white {
        font-weight: 700;
        color: #664e96;
        border-color: #664e96;
        background: transparent;
    }
    &.purple-white:hover {
        color: white;
        background: #664e96;
        border-color: #664e96;
    }
    &.white-purple {
        font-weight: 700;
        color: #664e96;
        border-color: white;
        background: white;
    }
    &.white-purple:hover {
        color: white;
        background: #664e96;
        border-color: white;
    }
}

.main_item{		
	justify-content: center;
	align-items: center;
}
.main_footer{
	background-color: rgb(232, 232, 232);
	
}

.red{
	background-color: red;
}
.blue{
	background-color: blue;
}


/* Complete */
#main_finish_overlay_container{
	background-color: rgb(255, 255, 255);
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);

	width: 50vw;
	height: 30vh;

	max-width: 500px;
	max-height: 500px;
	
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;	

	border-radius: 5px;
	z-index: 100;
}
.thumb{
	height: 70%;
	background-color: #1172c4;
	border-radius: 50%;	

	display: flex;
	justify-content: center;
	align-content: center;
}

.big-text{
	font-size: 18pt;
	margin: 20px;
	font-family: 'Noto Sans TC', sans-serif;
}

.main_footer_text{
	
	color: #6c277a;
	font-family: "Kiwi Maru", serif;
	font-weight: 400;
	font-style: normal;
	margin: 20px;
	&.small{
		font-size: 25pt;
	}
	&.big{
		font-size: 50pt;
	}
}

